  flex: 1 1 auto;
  display: grid;
  gap: 50px;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}
section .grid .project-container {
  display: flex;
  flex-direction: column;
  max-width: 450px;
  overflow: hidden;
}
section .grid .project-container .project-image {
  width: 100%;
  aspect-ratio: 1/1;
  object-fit: cover;
}
