  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 50px;
  padding-top: 50px;
  max-width: 1700px;
}
header .photo {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  box-shadow: 8px 8px 38px -18px rgb(0, 0, 0);
  z-index: 5;
}
header .photo h1 {
  font-size: 3rem;
  padding: 5px;
  margin: 0px;
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 0px;
}
header .photo img {
  float: left;
}
header .side {
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 100%;
  flex: 1 1 auto;
  padding: 30px;
  box-shadow: 8px 8px 38px -18px rgb(0, 0, 0);
  z-index: 5;
}
header .background {
  position: absolute;
  width: 200vw;
  height: 50%;
  top: -20%;
  left: -50%;
  z-index: -10;
  rotate: -10deg;
  z-index: -1;
  background-color: rgb(50, 87, 159);
  box-shadow: 8px 8px 38px -18px rgb(0, 0, 0);
}
header .icon-container {
  display: flex;
  justify-content: end;
  align-self: end;
  margin-top: auto;
  gap: 0.5rem;
