* {
  font-family: "roboto";
  font-size: 1.2rem;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "fancy";
}

h2 {
  font-size: 1.7rem;
}

html {
  overflow: hidden;
  overflow-y: scroll;
}

body {
  margin: 0;
  padding: 0;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  position: relative;
}

.box-shadow {
  box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.3);
}

.icon {
  height: 1.5rem;
  width: 1.5rem;
  transition: all 0.1s ease-in-out;
}

.icon:hover {
  transform: scale(1.2);
}

footer {
  display: flex;
  justify-content: center;
  margin-top: 7rem;
  padding: 0;
  width: 100%;
  background-color: rgb(50, 87, 159);
  color: white;
}
footer .content {
  max-width: 1300px;
  padding: 2rem;
  display: flex;
  flex-direction: row;
}
footer .content h2 {
  font-size: 2rem;
}
footer .content .left {
  display: flex;
  flex-direction: column;
}
footer .content .icon {
  margin-right: 1rem;
  height: 3rem;
  width: 3rem;
}
footer .content .right {
  height: 100%;
  width: auto;
